
.App {
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  form {
    margin: 20px 0;
  }
  
  input[type="text"] {
    padding: 10px;
    width: 60%;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-right: 10px;
  }
  
  input[type="submit"] {
    padding: 10px 15px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  input[type="submit"]:hover {
    background-color: #45a049;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  .todo-item {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    padding: 10px;
    margin: 10px 0;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .todo-item:hover {
    background-color: #efefef;
  }

.completed {
  text-decoration: line-through;
  color: #999;
}
